import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import DashBoard from '../views/dashboard.vue'
import ClientHome from '../views/clientHome.vue'
import CustomOrder from '../views/CustomOrder.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/login',
  },
  {
    path: '/',
    name: 'ClientHome',
    component: ClientHome,
    meta: {
      keepAlive: true // 需要被缓存
    },
  },
   {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/admin',
    name: 'Admin',
    component: DashBoard,
    children: [
      {
        path: 'products',
        name: 'Product',
        meta: { requiresAuth: true },
        component: () => import('../views/products.vue'),
      },
      {
        path: 'coupons',
        name: 'Coupon',
        meta: { requiresAuth: true},
        component: () => import('../views/coupons.vue'),
      },
      {
        path: 'order',
        name: 'adminOrder',
        component: () => import('../views/adminOrder.vue'),
      }
    ],
  },
  {
    path: '/customer_order',
    component: CustomOrder,
    children: [
      {
        path: '',
        name: "default",
        component: () => import('../views/CategoryProduct.vue'),
      },
      {
        path: 'category/:currentCategory',
        name: 'CategoryProduct',
        props: true,
        component: () => import('../views/CategoryProduct.vue'),
      },
      {
        path: 'product/:id',
        name: 'OneProduct',
        props: true,
        component: () => import('../views/OneProduct.vue')
      },
      {
        path: 'orderlist',
        name: 'OrderList',
        component: () => import('../views/CustomerOrderList.vue')
      },
      {
        path: 'formList',
        name: 'FormList',
        component: () => import('../views/CustomerForm.vue')
      },
      {
        path: 'paidList',
        name: 'PaidList',
        component: () => import('../views/CustomerPaid.vue')
      },
      {
        path: 'paidList/:orderId',
        name: 'PaidId',
        component: () => import('../views/GoToPaid.vue')
      }
    ],
    meta: {
      keepAlive: false // 需要被缓存
    },
  },
]

const router = new VueRouter({
  routes
})

export default router
