<script>
import $ from "jquery";
export default {
  data() {
    return {
        productsInCart: {},
        productsCount: 0,
        isLoading: false,
        inOrderList: false,
        inFormList: false,
    };
  },
  methods: {
    getProductCart(){
        console.log("begin to getProductCart")
        const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/cart`;
        console.log(api);
        var vm = this;
        vm.isLoading = true;
        this.$http.get(api).then(response => {
            console.log("response data:");
            console.log(response);
            if (response.data.success) {
                console.log(response.data);
                vm.productsInCart = Object.assign({},response.data.data);
                vm.productsCount = vm.productsInCart.carts.length;
            } else {
                console.log("get product cart failed");
                console.log(response.data.message);
            }
            vm.isLoading = false;
        });
    },
    deleteOneProductInCart(id) {
        console.log("begin to deleteOneProductInCart")
        const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/cart/${id}`;
        console.log(api);
        var vm = this;
        vm.isLoading = true;
        this.$http.delete(api).then(response => {
            console.log("response data:");
            console.log(response);
            if (response.data.success) {
                this.getProductCart();
            } else {
                console.log("delete product in cart failed");
                console.log(response.data.message);
            }
            vm.isLoading = false;
        });
    },
  },
  created() {
      console.log("ClientNavBar created");
      this.getProductCart();
      this.$bus.$on('message:refreshCart',this.getProductCart);
      if (this.$route.name === 'OrderList') {
        this.inOrderList = true;
      } else {
        this.inOrderList = false;
      }
      if (this.$route.name === 'FormList') {
        this.inFormList = true;
      } else {
        this.inFormList = false;
      }
  },
  mounted() {
    $(".dropdown").hover(
      function() {
        if ($(window).width() >= 992) {
          $(this)
          .find(".aaa")
          .stop(true, true)
          .delay(200)
          .fadeIn(500);
        }
          $(this)
          .find(".dropdown-menuStyle")
          .stop(true, true)
          .delay(200)
          .fadeIn(500);
        
      },
      function() {
          if ($(window).width() >= 992) {
            $(this)
            .find(".aaa")
            .stop(true, true)
            .delay(200)
            .fadeOut(500);      
          }
          $(this)
          .find(".dropdown-menuStyle")
          .stop(true, true)
          .delay(200)
          .fadeOut(500);
      }
    );
  },
  watch: {
    $route(to,from) {
      console.log("Yes client nav bar route change");
      console.log(to);
      console.log(from);
      if (to.name === 'OrderList') {
        console.log('Yes go to the OrderList');
        this.inOrderList = true;
      } else {
        console.log("Leave the OrderList");
        this.inOrderList = false;
      }
      if (to.name === 'FormList') {
        this.inFormList = true;
      } else {
        this.inFormList = false;
      }
      if (to.name === 'PaidList') {
        this.getProductCart();
      }
    }
  },
};
</script>
