<script>
//console.log("import script");
//import Swiper from "swiper";
import $ from "jquery";
import lightbox from "../assets/lightbox-plus-jquery.min";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import footerSection from "@/components/footerSection.vue"

export default {
  data() {
    return {
      swiperOption: {
        autoplay: true,
        speed: 500,
        loop: true,
        effect: "cube",
        cubeEffect: {
          slideShadows: false, //开启slide阴影。默认 true。
          shadow: true, //开启投影。默认 true。
          shadowOffset: 30, //投影距离。默认 20，单位px。
          shadowScale: 0.2 //投影缩放比例。默认0.94。
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      swiperOption2: {
        slidesPerView: 5,
        spaceBetween: 30,
        autoplay: true
      }
    };
  },
  components: {
    swiper,
    swiperSlide,
    footerSection
  },
  methods: {
    scrolltoTop(){
        $("html, body").animate(
        {
            scrollTop: $("body").offset().top
        },1000,function() {
            $(".toTop").hide();
        }
        );
    },
    adjustSlidesPerView(ww) {
        var vm = this;
        console.log("resize window function call")
        console.log(vm);
        console.log(vm.$refs);
        console.log(vm.$refs.mylogoSwiper);
        vm.$nextTick(() => { 
            console.log("nextTick")
            console.log(vm.$refs.mylogoSwiper)
            if (vm.$refs.mylogoSwiper !== undefined) {
                if (ww > 1000) {
                    console.log("ww > 1000");
                    vm.$refs.mylogoSwiper.swiper.params.slidesPerView = 5; 
                }
                if (ww > 468 && ww <= 1000) {
                    console.log("468 < ww <= 1000");
                    vm.$refs.mylogoSwiper.swiper.params.slidesPerView = 3;
                }
                if (ww <= 468) {
                    console.log("ww <= 468");
                    vm.$refs.mylogoSwiper.swiper.params.slidesPerView = 2;
                    
                }
            } 
        })
    }
  },
  created() {
    console.log("clientHome created");
    lightbox.option({
      resizeDuration: 200,
      wrapAround: true
    });
  },
  destroyed() {
    console.log("clientHome destroyed");
  },
  activated() {
    console.log("clientHome activated");
  },
  mounted() {
    var vm = this;
    console.log("clientHome mounted");
    console.log(vm.$refs);
    $(".dropdown").hover(
      function() {
        $(this)
          .find(".dropdown-menu")
          .stop(true, true)
          .delay(200)
          .fadeIn(500);
      },
      function() {
        $(this)
          .find(".dropdown-menu")
          .stop(true, true)
          .delay(200)
          .fadeOut(500);
      }
    );
    $(window).scroll(function() {
        var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
        if (scrollTop > 100) {
            $(".toTop").show();
        }
        $(".animatedme").each(function() {
        //console.log("herehere")
        var thisPos = $(this).offset().top;
        if ($(window).height() + scrollTop >= thisPos) {
            $(this).addClass("fadeIn");
        } else {
            $(this).removeClass("fadeIn");
        }
        });
    });
    $(window).resize(function () { 
        var ww = $(window).width();
        vm.adjustSlidesPerView(ww);
    });
    },
    beforeUpdate() {
        console.log("before update")
    },
    updated() {
        console.log("updated")
    },
};
</script>


