<template>
  <div>
    <navbar class="clearfix"></navbar>
    <allert></allert>
    <header class="bgCusColor">
      <div class="container px-0">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb bgCusColor pl-0">
            <li class="breadcrumb-item">
              <router-link to="/">Home</router-link>
            </li>
            <li
              v-if="categoryName != '' && oneProductName ==''"
              class="breadcrumb-item active"
              aria-current="page"
            >{{categoryName}}</li>
            <li v-else-if="categoryName != '' && oneProductName !=''" class="breadcrumb-item">
              <a href="#" @click.prevent="goToPreviousPage">{{categoryName}}</a>
            </li>

            <li
              v-if="oneProductName !=''"
              class="breadcrumb-item active"
              aria-current="page"
            >{{oneProductName}}</li>
            <li
              v-if="inOrderList"
              class="breadcrumb-item active"
              aria-current="page"
            >cart</li>
            <li
              v-if="inFormList"
              class="breadcrumb-item active"
              aria-current="page"
            >form</li>
            <li
              v-if="$route.name === 'PaidList'"
              class="breadcrumb-item active"
              aria-current="page"
            >Paid</li>
            <li v-if="$route.params.hasOwnProperty('orderId')" class="breadcrumb-item">
              <router-link to="/customer_order/paidList">Paid</router-link>
            </li>
            <li v-if="$route.params.hasOwnProperty('orderId')" class="breadcrumb-item active"
              aria-current="page">
              {{$route.params.orderId}}
            </li>    
          </ol>
        </nav>

        <router-view></router-view>
      </div>
    </header>
    <footerSection />
  </div>
</template>
<script>
import navbar from "@/components/ClientNavBar.vue";
// import sideBar from "@/components/ClientSideBar.vue";
import footerSection from "@/components/footerSection.vue";
import allert from "@/components/allertMessage.vue";

export default {
  data() {
    return {
      categoryName: "",
      oneProductName: "",
      inOrderList: false,
      inFormList: false,
    };
  },
  
  components: {
    navbar,
    // sideBar,
    footerSection,
    allert
  },
  beforeCreate() {
    console.log("customorder before created");
  },
  created() {
    console.log("customorder created");
    console.log(this.$route.params);
    if (this.$route.params.hasOwnProperty('id')){
        console.log('id property 存在')
        console.log(this.$route.params.id)
        this.getOneProductById(this.$route.params.id);
    } else if (this.$route.params.hasOwnProperty('currentCategory')) {
        console.log('currentcategory 存在')
        this.categoryName = this.$route.params.currentCategory;
    } else {
        console.log('Category name default value from CategoryProduct.vue');
    }

    if (this.$route.name === 'default') {
        this.categoryName = 'refresh';
    }

    if (this.$route.name === 'OrderList') {
      this.inOrderList = true;
    } else {
      this.inOrderList = false;
    }
    if (this.$route.name === 'FormList') {
      this.inFormList = true;
    } else {
      this.inFormList = false;
    }
  },
  beforeDestroy() {
    console.log("customorder before destroy");
  },
  beforeUpdate() {
    console.log("customorder before update");
  },
  updated() {
    console.log("customoreder update");
  },
  destroyed() {
    console.log("custom order destroy");
  },
  mounted() {
    console.log("custom order mounted");
    this.$on("CategoryName", this.updateCategoryName);
    this.$on("OneProductName", this.updateOneProductName);
    this.$on("cleanOneProduct", this.cleanOneProductName);
  },
  methods: {
    getOneProductById(id) {
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/product/${id}`;
            console.log(api);
            var vm = this;
            this.$http.get(api).then(response => {
                console.log("response data:");
                console.log(response);
                if (response.data.success) {
                    vm.categoryName = response.data.product.category.replace(/[^\x00-\x7F]/g, "").toLowerCase();
                    vm.oneProductName = response.data.product.title;
                    console.log(vm.categoryName);
                } else {
                    console.log("Get OneProduct fail");
                    console.log(response.data.message);
                }
            });
    },
    updateCategoryName(name) {
      console.log("update Category name in customorder");
      this.categoryName = name;
    },
    updateOneProductName(name) {
      console.log("update OneProduct name in customorder");
      this.oneProductName = name;
      console.log(this.oneProductName);
    },
    cleanOneProductName() {
      console.log("clean one product name");
      this.oneProductName = "";
      console.log(this.oneProductName);
    },
    goToPreviousPage() {
      this.oneProductName = "";
      console.log("go to previous page");
      this.$router.push({path:`/customer_order/category/${this.categoryName}`})
        
      // this.$router.go(-1);
    },
  },
  watch: {
    $route(to,from) {
      console.log("route change")
      console.log(to);
      console.log(from);
      if (to.name === 'OrderList') {
        console.log('Yes go to the OrderList');
        this.oneProductName = "";
        this.categoryName = "";
        this.inOrderList = true;
        this.inFormList = false;
      } else if (to.name === 'FormList') {
        console.log('Yes go to the FormList');
        this.oneProductName = "";
        this.categoryName = "";
        this.inOrderList = false;
        this.inFormList = true;
      } else if (to.name === 'CategoryProduct') {
        console.log('Yes go to the Category');
        this.categoryName = this.$route.params.currentCategory;
        this.oneProductName = '';
        this.inOrderList = false;
        this.inFormList = false;
      } else if (to.name === 'OneProduct') {
        console.log('Yes go to OneProduct');
        this.inOrderList = false;
        this.inFormList = false;
      } else if (to.name === 'default') {
        console.log("Yes go to default");
        this.categoryName = 'refresh';
        this.oneProductName = '';
        this.inOrderList = false;
        this.inFormList = false;
      } else if (to.name === 'PaidList') {
        console.log("Yes go to PaidList");
        this.categoryName = "";
        this.oneProductName = "";
        this.inOrderList = false;
        this.inFormList = false;
      }
    }
  },
};
</script>

<style scoped>
.bgCusColor {
  background-color: #f8f8f8;
}
</style>