<template>
    <div>
        <nav class="navbar navbar-expand-lg sticky-top justify-content-between">
            <router-link class="col-sm-3 col-md-2 mr-0" to="/">
                <img src="../assets/img/op-logo-w.png" width="110" height="55" alt />
            </router-link>
            <form class="form-inline my-2 my-lg-0 mr-2">
                <button class="btn btn-success my-2 my-sm-0" type="submit" @click.prevent="logout">LogOut</button>
            </form>
        </nav>
    </div>
</template>
<script>
export default {
  methods: {
    logout() {
      const api = `${process.env.VUE_APP_APIPATH}/logout`;
      console.log("logout .......");
      console.log(api);
      var vm = this;
      this.$http.post(api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          vm.$router.push("/login");
        } else {
          vm.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
    }
  }
};
</script>
<style scoped>
nav.navbar {
  background-color: rgba(36, 33, 74, 0.88);
}
</style>
